	var schedule = function() {
		var headerHeight = $(".schedule .results .outbound header").outerHeight() + $(".schedule_container > header").outerHeight();
		var windowHeight = 0;
		function control(e) {
			
		}
		function resizeHandler(e) {
			windowHeight = $(window).innerHeight();
			// headerHeight = $(".schedule .results .selecting header").outerHeight() + $(".schedule_container > header").outerHeight();
			// var matrix = $(".schedule_container .results").css("transform").split(", ");
			var extraHeaderHeight = 0;
			var headerheight = 0;
			// headerHeight=parseInt(matrix[5]) + parseInt($(".schedule_container .results").css("top"));

			
			console.log("resizing", headerHeight);	
			if ($(".schedule_container").hasClass("step1")) {
				extraHeaderHeight = $(".outbound header").outerHeight();
				headerHeight = parseInt($(".schedule_container .results").css("top")) + 5;
				$(".outbound ul").css("height",windowHeight-(headerHeight+extraHeaderHeight));
			} else if ($(".schedule_container").hasClass("step2")) {
				extraHeaderHeight = $(".returning header").outerHeight();
				headerHeight = parseInt($(".schedule_container .results").css("top")) + 95;
				$(".returning ul").css("height",windowHeight-(headerHeight+extraHeaderHeight));
			} else if ($(".schedule_container").hasClass("step3")) {
				extraHeaderHeight = $(".ticket_options header").outerHeight();
				headerHeight = parseInt($(".schedule_container .results").css("top")) + 182;
				$(".ticket_options ul").css("height",windowHeight-(headerHeight+extraHeaderHeight));
			} else if ($(".schedule_container").hasClass("step4")) {
				extraHeaderHeight = 0;
				headerHeight = parseInt($(".schedule_container .results").css("top")) + 520;
				// $(".outbound ul, .returning ul, .ticket_options ul").css("height",windowHeight-(headerHeight+extraHeaderHeight));
			}

			$(".outbound ul, .returning ul, .ticket_options ul").css("height",windowHeight-(headerHeight+extraHeaderHeight));
			// $(".results").css("height",windowHeight-headerHeight);
		}
		return {
			Init: function() {
			
				$(window).bind("resize",resizeHandler)
				// $(".stations, .outbound_train, .returning_train",$("itinerary")).


				$(".outbound li").bind("click",function() {
					$(this).siblings().addClass("faded");

					$(".itinerary .outbound datetime").first().html($("datetime",$(this)).first().html());
					$(".itinerary .outbound datetime").last().html($("datetime",$(this)).last().html());

					// $(".outbound").addClass("final").removeClass("selecting");
					// $(".returning").addClass("selecting");
					$(".schedule_container").removeClass("step1 step2 step3 step4");;
					$(".schedule_container").addClass("step2");
					resizeHandler();
				});
				$(".returning li").bind("click",function() {
					$(this).siblings().addClass("faded");
					// $(".returning").addClass("final").removeClass("selecting");
					// $(".ticket_options").addClass("selecting");
					
					$(".schedule_container").removeClass("step1 step2 step3 step4");;
					$(".schedule_container").addClass("step3");

					$(".itinerary .returning datetime").first().html($("datetime",$(this)).first().html());
					$(".itinerary .returning datetime").last().html($("datetime",$(this)).last().html());
					resizeHandler();
				});
				
				$(".ticket_options li").bind("click",function() {
					$(this).siblings().addClass("faded");
					// $(".ticket_options").addClass("final").removeClass("selecting");

					$(".schedule_container").removeClass("step2").removeClass("step3").removeClass("step4");
					$(".schedule_container").addClass("step4");

					if ($(this).hasClass("upgrade")) {
						var ticket = $(".name", $(this).prev() ).html()+ " First";
					} else {
						var ticket = $(".name",$(this)).html();
					}
					var price = $(".price",$(this)).html();
					$(".itinerary .ticket").html(ticket);
					$(".itinerary .price").html(price);
					resizeHandler();
				});



				// going back controls
				$(".schedule .stations").bind("click",function(e) {
					$(".schedule_container").removeClass("step2").removeClass("step3").removeClass("step4");
					$(".outbound li, .returning li, .ticket_options li").removeClass("faded");
					// $(".outbound, .returning, .ticket_options").removeClass("final").removeClass("selecting");
					// $(".outbound").addClass("selecting");
					closeSchedule();
					setTimeout(function() {
						enterPlanning();
					},1200);
					e.preventDefault();
				});
				$(".itinerary .outbound_train, .itinerary datetime.outbound, .itinerary div.outbound").bind("click",function() {
					$(".schedule_container").removeClass("step1 step2 step3 step4");
					$(".schedule_container").addClass("step1");
					// $(".returning").removeClass("final").removeClass("selecting");
					// $(".ticket_options").removeClass("final").removeClass("selecting");
					$(".outbound li, .returning li, .ticket_options li").removeClass("faded");
				
					resizeHandler();
				});

				$(".itinerary .returning_train, .itinerary datetime.returning, .itinerary div.returning").bind("click",function() {
					
					$(".schedule_container").removeClass("step1 step2 step3 step4");;
					$(".schedule_container").addClass("step2");

					// $(".returning").removeClass("final").addClass("selecting");
					// $(".ticket_options").removeClass("final").removeClass("selecting");
					
					$(".outbound li, .returning li, .ticket_options li").removeClass("faded");
					setTimeout(function() {
						// $(".returning").removeClass("final").addClass("selecting");
					},300);
					resizeHandler();
				});

				$("h3.ticket").bind("click",function() {

					$(".schedule_container").removeClass("step1 step2 step3 step4");;
					$(".schedule_container").addClass("step3");

					// $(".ticket_options").removeClass("final").addClass("selecting");
					$(".ticket_options li").removeClass("faded");
					setTimeout(function() {
						// $(".ticket_options").removeClass("final").addClass("selecting");
					},300);
					resizeHandler();
				
				});
			},
			sizeUp: function() {
				resizeHandler();
			}
		}
	}();
$(function() {
	schedule.Init();
});
	
	
